import './App.css';
import Header from "./components/Header";
import Banner from "./components/Banner";
import AboutUs from "./components/AboutUs";

import Footer from "./components/Footer";
import Notice from "./components/Notice";

import Highlight from "./components/Highlight";
import Event from "./components/Event";
import Faculties from "./routes/Faculties";

import Schlorship from "./routes/Schlorship";
import Payments from "./routes/Payments";
import Career from "./routes/Career";
import Placement from "./routes/Placement";
import Course from "./routes/Course";
import AboutInst from "./routes/AboutInst";

import Contact from "./routes/Contact";






import {BrowserRouter as Router ,Switch,Route} from "react-router-dom";





function App() {

  return (

    <Router>
    <div >


      <Header/>

      <Switch>
      
          
          <Route path= "/" exact component={Body} />
          <Route path= "/Faculties" exact component={Faculties} />
          <Route path= "/Schlorship" exact component={Schlorship} />
         <Route path= "/Payments" exact component={Payments} />
          <Route path= "/Career" exact component={Career} />
           <Route path= "/Placement"  exact component={Placement} />
            <Route path= "/AboutInst"  exact component={AboutInst} />

            <Route path= "/Contact"  exact component={Contact} />



            <Route path= "/Course" exact component={Course} />

           <Route path= "/Course/:course" exact component={Course} />


           


            


      

      </Switch>

            <Footer />

    </div>

    </Router>
  );
}






function Body() {
  return (
    <div className="App">
        
        
       
        <Banner />
        <Notice />
        <div className="main_events">
          <h1>EVENTS</h1>

        </div>
        <Event />
         <div className="main_events">
          <h1>HIGHLIGHTS</h1>

        </div>

  

        <Highlight />
        <AboutUs />


        


    </div>
  );
}

export default App;
