import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import MenuIcon from '@material-ui/icons/Menu';
import Collapse from '@material-ui/core/Collapse';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import ListSubheader from '@material-ui/core/ListSubheader';
import HomeIcon from '@material-ui/icons/Home';
import SchoolIcon from '@material-ui/icons/School';
import SchoolSharpIcon from '@material-ui/icons/SchoolSharp';
import CastForEducationSharpIcon from '@material-ui/icons/CastForEducationSharp';
import PermContactCalendarIcon from '@material-ui/icons/PermContactCalendar';
import WorkIcon from '@material-ui/icons/Work';
import TouchAppIcon from '@material-ui/icons/TouchApp';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import ClassIcon from '@material-ui/icons/Class';
import InfoIcon from '@material-ui/icons/Info';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import PaymentIcon from '@material-ui/icons/Payment';
import GroupIcon from '@material-ui/icons/Group';

import {Link} from "react-router-dom"; 

const useStyles = makeStyles((theme) => ({
  list: {
    width: 250,
  

  },
  fullList: {
    width: 'auto',
  },
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

export default function SwipeableTemporaryDrawer() {
  const classes = useStyles();


  const class_list=["Class 1","Class 2" ," Class 3","Class 4","Class 5","Class 6","Class 7","Class 8"];
  const diploma_course_list = ["DCA","PGDCA","ITI Fitter" ," ITI Electrician"]
  const training_list = ["Major Training","Minor Training" ,"CPCT","Tally","C C++ Java"]

  const [acadamic, set_acadamic] = React.useState(true);
  const [school, set_school] = React.useState(true);
  const [diploma_courses, set_diploma_courses] = React.useState(true);
  const [trainings, set_trainings] = React.useState(true);


  const handleClick = () => {
    set_acadamic(!acadamic);
  };

  const handleTraining = () => {
    set_trainings(!trainings);
  };

  const handleDiploma= () => {
    set_diploma_courses(!diploma_courses);
  };

  const handleAcadamic = ()=>{
    set_school(!school);
  }

   const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };


  const list = (anchor) => (
    <div
     className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onKeyDown={toggleDrawer(anchor, false)}
    >
      
          <List
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      
    >

       <Link  to="/">
      <ListItem button>
        <ListItemIcon>
          <HomeIcon />
        </ListItemIcon>
        <ListItemText secondary="Home" />
      </ListItem>

      </Link>
      

      <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <AccountBalanceIcon />
        </ListItemIcon>

        <ListItemText secondary="Acadamics" />
        {acadamic ? <ExpandLess /> : <ExpandMore />}
      </ListItem>

      <Divider />
      <Collapse in={acadamic} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
        
        <ListItem button onClick={handleAcadamic}>
        <ListItemIcon>
          <CastForEducationSharpIcon />
        </ListItemIcon>

        <ListItemText secondary="School" />
        {school ? <ExpandLess /> : <ExpandMore />}
      </ListItem>


      <Collapse in={school} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>

          {class_list.map((item,index) => (

                          <div  key={item}>
                              <li><Link  to={`/Course/${item}`}><ListItem button><ListItemText secondary={item} /></ListItem></Link></li>
                          </div>
            ))}
        
        </List>
      </Collapse>
      <Divider />
        </List>


      
           <ListItem button onClick={handleDiploma}>
             <ListItemIcon>  <SchoolIcon />  </ListItemIcon>
             <ListItemText secondary="Diploma Courses" />
                 {diploma_courses ? <ExpandLess /> : <ExpandMore />}
            </ListItem>


                <Collapse in={diploma_courses} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                  {diploma_course_list.map((item,index) => (

                          <div  key={item}>
                              <li><Link  to={`/Course/${item}`}><ListItem button><ListItemText secondary={item} /></ListItem></Link></li>
                          </div>
                 ))}
              </List>
              </Collapse>
              <Divider />


               <ListItem button onClick={handleTraining}>
                <ListItemIcon>  <ClassIcon />  </ListItemIcon>
             <ListItemText secondary="Trainings" />
                 {trainings ? <ExpandLess /> : <ExpandMore />}
              </ListItem>
              <Collapse in={trainings} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                  {training_list.map((item,index) => (

                          <div  key={item}>
                              <li><Link  to={`/Course/${item}`}><ListItem button><ListItemText secondary={item} /></ListItem></Link></li>
                          </div>
                 ))}
              </List>


                </Collapse>
                   <Divider />
      </Collapse>
    </List>

   
      <List>

         


           <Link  to="/Placement">
          <ListItem button >
           

            <ListItemIcon><WorkIcon /></ListItemIcon>
            <ListItemText secondary="Placements" />
            
          </ListItem>
          </Link>

           <Link  to="/Payments">
          <ListItem button >
            <ListItemIcon><PaymentIcon /></ListItemIcon>
            <ListItemText secondary="Payments" />
          </ListItem>

          </Link>


             <Link  to="/Faculties">

            <ListItem button >
            <ListItemIcon><GroupIcon /></ListItemIcon>
            <ListItemText secondary="Faculties" />
          </ListItem>
              </Link>

           <Link  to="/Schlorship">
           <ListItem button >
            <ListItemIcon><LocalAtmIcon/></ListItemIcon>
            <ListItemText secondary="Schlorship" />
          </ListItem>
            </Link>

           <Link  to="/Contact">
           <ListItem button >
            <ListItemIcon><PermContactCalendarIcon /></ListItemIcon>
            <ListItemText secondary="Contact Us" />
          </ListItem>
              </Link>


           <Link  to="/Career">
           <ListItem button >
            <ListItemIcon><TouchAppIcon /></ListItemIcon>
            <ListItemText secondary="Career" />
          </ListItem>
              </Link>

          <Link  to="/AboutInst">
           <ListItem button >
            <ListItemIcon><InfoIcon /></ListItemIcon>
            <ListItemText secondary="About Us" />
          </ListItem>
            </Link>
      </List>
    </div>
  );

  return (
    <div>
      {['right'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}><MenuIcon /></Button>
          <SwipeableDrawer
            anchor="right"
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
    </div>
  );
}
