import React from 'react';
import AwesomeSlider from 'react-awesome-slider';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import 'react-awesome-slider/dist/styles.css';
import "../App.css";


const AutoplaySlider = withAutoplay(AwesomeSlider);
 

function Slider() {
	return (
		<div className="slider">
			<AutoplaySlider
		    play={true}
		    cancelOnInteraction={false} // should stop playing on user interaction
		    interval={6000}
		    buttons={true}
		  >
		    <div data-src="https://5.imimg.com/data5/DJ/PV/GLADMIN-64970456/computer-lab-class-500x500.png" />
		    <div data-src="https://www.pimrindore.ac.in/images/computer-lab.jpg" />
		    <div data-src="https://almayasabdam.com/wp-content/uploads/2018/08/general-computer-class-photo-500x332.jpg" />
		  </AutoplaySlider>
		</div>
	)
}

export default Slider